
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Search from 'vux/src/components/search/index.vue'


export default {
components: {
Timeline, TimelineItem, XCircle,
Scroller,
LoadMore,
Search
},
data() {
return {
pulldown: {
content: '',
height: 60,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: 'Load...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup: {
content: '',
pullUpHeight: 50,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: 'Loading...',
clsPrefix: 'xs-plugin-pullup-'
},
scrollTop: 0,
onFetching: false,
bottomCount: 10,
search: '',
strokeColor2: '#3FC7FA',
allLoaded: false,
check: false,
selectIds: [],
advancedQuery: {
enable: false,//
startDate: '',
endDate: '',
projUid: ''
},
noMore: true,
items: {
textAlign: 'left',
padding: '0 10px',
height: '2.5rem',
borderBottom: '1px solid #ccc',
display: 'flex',
justifyContent: 'center',
alignItems: 'center'
},
operateType: null,
category:null,
isListView: true,
pageCount: 0,
onFetching: false,
noMore: false,
list: [],
searchList: [],
data: [],
isEnd: false,
isStatistics: false,
pageSize: 10,
pageType: ''
}
},
mounted() {

this.$nextTick(() => {

this.$refs.scrollerBottom.reset({ top: 0 })
})
},
activated() {

this.util.init(this);
var _this = this;
var isStatisticsParam = this.util.getUrlParam("isStatistics");
this.isStatistics = (this.validUtil.isNotEmpty(isStatisticsParam) && isStatisticsParam == "true") ? true : false;
var operateType = this.util.getUrlParam("operateType");
if (this.util.isNotEmpty(operateType)) {
this.operateType = operateType;
}
var category = this.util.getUrlParam("type");
if (this.util.isNotEmpty(category)) {
this.category = category;
}
var type = this.util.getUrlParam("type");
if (this.util.isNotEmpty(type)) {
this.type = type;
this.noMore = false;
this.isEnd = false;
this.type = type;
_this.data = [];
if (this.type == 'finished') {
$(document).attr("title", "");
this.pageType = 'read';
} else {
$(document).attr("title", "");
this.pageType = 'unRead';
}
}

if (_this.data.length == 0) {

_this.loadData();
} else {
if (this.$store.state.operate == 'save') {

_this.loadTop();
}
}

},
methods: {
getProjIcon(item) {

return this.util.getBaseUrl() + '/attachment/look?id=' + item.picture + '&access_token=' + this.util.getAccessToken();
},
percent(item) {
return item.percent == 100 ? "background-color:rgb(90, 214, 145)" : (item.percent > 50 ? "background-color:#c1c16b;" : "background-color:red");
},
isExistProjIcon(item) {

return this.util.isNotEmpty(item.picture) ? true : false;

},
visitOpen() {
this.visit = true;
},
onScrollBottom() {
if (this.isEnd) {
return;
}
console.info("******onScrollBottom*******" + this.data.length);
if (this.onFetching) {

} else {
this.onFetching = true;
this.noMore = false;
if (this.data.length == 0) {
this.loadData();
} else {
this.loadData("downLoad", this.data[this.data.length - 1].createTime);
}

}
}, loadData(type, creatTime) {



var param = {
createTime: creatTime == undefined ? null : creatTime,
type: type == undefined ? null : type,
category: this.category == undefined ? null : this.category,
userUid: this.$store.state.userUid,
assignee: this.$store.state.userUid,
userId: this.sessionUtil.getUserId(),
companyUid: this.$store.state.companyUid,
isStatistics: this.isStatistics,
search: this.util.isNotEmpty(this.search) ? this.search : null,
company_id: this.sessionUtil.getCompanyId()
};

if (this.util.isNotEmpty(this.operateType)) {
param.operateType = this.operateType;
}
if (this.util.isNotEmpty(this.type)) {
param.type = this.type;
}

if (this.advancedQuery.enable) {
param.advancedQueryEnable = this.advancedQuery.enable;
param.startDate = this.advancedQuery.startDate;
param.endDate = this.advancedQuery.endDate;
param.projUid = this.advancedQuery.projUid;
param.search = null;
}
var state = this.$store.state;
var _this = this;
if ((_this.util.isNotEmpty(_this.search) || _this.advancedQuery.enable) && type == undefined) {
_this.util.mask("...");
}

_this.onFetching = true;



this.util.post('/wx/workflowAction!findMyCopy.action?pageSize=' + _this.pageSize, param, function (res) {

if ((_this.util.isNotEmpty(_this.search) || _this.advancedQuery.enable) && type == undefined) {
_this.util.unmask();
}


if (res.data == undefined || res.data.length == 0) {
if (type == undefined && (_this.util.isNotEmpty(_this.search) || _this.advancedQuery.enable)) {
_this.searchList = [];
_this.data = [];
}
if (type == 'downLoad') {

_this.allLoaded = true;// 
if (res.ifEnd) {
_this.isEnd = true;
}
}
if (type == 'upLoad') {
_this.data = [];
_this.allLoaded = true;// 
if (res.ifEnd) {
_this.isEnd = true;
}
}

_this.noMore = true;


_this.$nextTick(() => {
_this.$refs.scrollerBottom.reset()
})
_this.onFetching = false

_this.$refs.scrollerBottom.donePullup();
_this.$refs.scrollerBottom.donePulldown();
var dom = _this.$refs.scrollerBottom.$el;
dom = $(dom).find('.xs-container')[0];
$(dom).css('transform', 'translateX(0px) translateY(0px) translateZ(0px) scale(1, 1)');

return;
}
if (type == undefined) {
if (_this.validUtil.isNotEmpty(_this.search) || _this.advancedQuery.enable) {
var dom = _this.$refs.scrollerBottom.$el;

dom = $(dom).find('.xs-container')[0];


$(dom).css('transform', 'translateX(0px) translateY(0px) translateZ(0px) scale(1, 1)');


_this.searchList = res.data;

} else {
_this.list = res.data;
}

_this.data = res.data;

_this.onFetching = false;
_this.$nextTick(() => {
_this.$refs.scrollerBottom.reset()
});
if (res.ifEnd) {
_this.isEnd = true;
}
_this.$refs.scrollerBottom.donePullup();
_this.noMore = true;
return null;

} else if (type == 'upLoad') {



_this.data = res.data;
if (_this.validUtil.isNotEmpty(_this.search) || _this.advancedQuery.enable) {
_this.searchList = _this.data;

} else {
_this.list = _this.data;
}
_this.$nextTick(() => {
_this.$refs.scrollerBottom.reset()
});


_this.$refs.scrollerBottom.donePulldown();
_this.onFetching = false
} else if (type == 'downLoad') {

_this.data = _this.data.concat(res.data);
if (_this.validUtil.isNotEmpty(_this.search) || _this.advancedQuery.enable) {
_this.searchList = _this.data;

} else {
_this.list = _this.data;
}

_this.$nextTick(() => {
_this.$refs.scrollerBottom.reset()
});

_this.onFetching = false;
_this.$refs.scrollerBottom.donePullup();

if (res.ifEnd) {
_this.isEnd = true;
}
_this.noMore = true;

}

});
},
loadTop() {
this.onFetching = true;


var xmArr = this.data;



if (xmArr.length == 0) {

this.loadData();
return;
}

var startDate = xmArr[xmArr.length - 1].createTime;

this.loadData('upLoad');
},
showDetail(item) {
this.$store.state.temp = item;
console.info(item);

if (this.util.isNotEmpty(item.formKey)) {
var status = "";
if (this.util.isNotEmpty(item.procInstId) && this.util.isNotEmpty(item.id)) {


}
if (item.formKey == 'apply_goods') {
this.$router.push({ path: '/apply/applyGoods', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'expense') {
this.$router.push({ path: '/movebx', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'leave') {
this.$router.push({ path: '/addLeave', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'apply_purchase') {
this.$router.push({ path: '/apply/applyPurchase', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'apply_borrowing') {
this.$router.push({ path: '/apply/applyBorrowing', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'apply_payment') {
this.$router.push({ path: '/apply/applyPayment', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'apply_workover') {
this.$router.push({ path: '/apply/applyWorkover', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}else if (item.formKey == 'apply_common') {
this.$router.push({ path: '/apply/applyCommon', query: { id: item.businessKey, _procInstId: item.procInstId, _procDefId: item.procDefId, _taskId: item.id, _taskType: item.category, _currentActId: item.actId, isReadOnly: true } });
}

}else{
this.$router.push({ path: '/processForm', query: { id: item.businessKey,_procDefId: item.procDefId,_procInstId:item.procInsId,_taskId:item.id,_currentActId:item.taskDefKey,_startUserId:item.startUserId,_assignee:item.assignee,_taskType:0,_pageType:this.pageType,_procDefName: encodeURI(item.procDefName),_actName: encodeURI(item.actName),_subject: this.util.isNotEmpty(item.subject)?encodeURI(item.subject):''}});
}

/*
var _this = this;
this.util.mask();
this.util.post('/mobile/wx/safety/getImproveBillDetail', { checkId: item.id }, function (result) {
_this.util.unmask();
_this.$router.push({
name: 'safetyImproveBillShow', query: {
improveBill: item,
improveBillDet: result.improveDetail, replyRecords: result.replyRecords
}
});
});
*/
},
contains(arr, obj) {
var i = arr.length;
while (i--) {
if (arr[i] === obj) {
return true;
}
}
return false;
},
deleteData() {

},
deleteGou(item) {


}, add() {
this.$router.push({ name: 'proj/workLog/info', query: { isReadOnly: false } });
}, query() {
this.$store.state.preVue = this;
this.$router.push({ name: 'proj/workLog/query', query: {} });
},
handleClick() {

if (!this.validUtil.isNotEmpty(this.search)) {
if (this.list.length > 0) {
this.data = this.list;
this.allLoaded = false;
this.noMore = true;
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
});
return;
}

}
this.loadData(null, null);


}
}
}



